.card {
  width: 200px;
  padding: 32px 16px 16px;
  border: 1px solid $color-gray;
  border-radius: 5px;

  @include transition;
  &:hover {
    transform: scale(1.2);
  }

  &__img {
    display: block;
    height: 134px;
    width: 160px;
    margin: 0 auto 40px;
  }

  &__name-of-product {
    color: $text-main-color;
    font-size: 12px;
    margin-bottom: 4px;
    font-weight: 500;
    line-height: 18px;

    @include transition;
  }

  &:hover .card__name-of-product {
    color: #34569b;
  }

  &__code-of-product {
    color: $text-secondary-color;
    font-size: 10px;
    line-height: 14px;
  }

  &__stars {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 16px;
    margin-bottom: 24px;
  }

  &__reviews {
    color: $text-main-color;
    font-size: 10px;
    line-height: 14px;
  }

  &__cost {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 16px;
  }
  &__name {
    color: $text-secondary-color;
    font-size: 12px;
    line-height: 18px;
  }
  &__price {
    color: $text-main-color;
    font-size: 16px;
    font-weight: 700;
    line-height: 18px;
  }

  &__button {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    text-transform: uppercase;
    text-decoration: none;
    color: $color-white;
    background-color: $color-blue-accent;
    border: 1px solid $color-blue-accent;
    border-radius: 5px;
    width: 100%;
    height: 40px;

    transition:
      background-color 0.3s ease,
      color 0.3s ease;

    &:hover {
      color: $color-blue-accent;
      background-color: $color-white;
    }
  }
}
